.App {
  padding: 30px;
  text-align: center;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.main-form-conteiner {
  text-align: right;
  padding-right: 30px;
}
.result-conteiner {
  text-align: left;
  vertical-align: middle;
  padding-left: 30px;
}
.MuiTableContainer-root {
  margin: 30px auto;
  grid-column-start: span 2;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.main-form {
  list-style: none;
}
.main-form li {
  margin-top: 10px;
}
.statistic-box {
  display: block;
  grid-column-start: span 2;
}
.statistic-list {
  list-style: none;
}
